/* Estilo global */
body {
  font-family: Arial, sans-serif;
  background-color: white;
  margin: 0;
  padding: 0;
  color: #333;
}

/* Estilo do contêiner principal */
.app {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

.header h1 {
  color: #000a0c;
}

.header p {
  color: #000a0c;
  font-size: 16px;
}

/* Estilo das seções */
.portfolio-section {
  margin-bottom: 30px;
}

.portfolio-section h2 {
  text-align: center;
  color: #000a0c;
  border-bottom: 2px solid #1137dd;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

.item {
  margin: 10px 0;
}

.item a {
  text-decoration: none;
  color: #000a0c;
  font-weight: bold;
}

.item a:hover {
  color: #d90429;
  text-decoration: underline;
}

/* Responsividade */
@media (max-width: 768px) {
  .app {
    padding: 10px;
  }

  .header h1 {
    font-size: 24px;
  }

  .portfolio-section h2 {
    font-size: 20px;
  }
}
